import React, { Component } from 'react';
import { Upload, message, Modal } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import { UploadFile } from 'antd/lib/upload/interface';
import {
  ResponseDataInterface,
  ResponseErrorInterface,
} from '../../services/commonTypes';

interface VedioUploadProps {
  value?: string;
  onChange?: (vedioUrl: string) => void;
}

interface VedioState {
  showModal: boolean;
}

export default class VedioUpLoad extends Component<
  VedioUploadProps,
  VedioState
> {
  state: VedioState = {
    showModal: false,
  };

  private getUploadContent() {
    if (this.props.value) {
      return null;
    } else {
      return (
        <div>
          <PlusOutlined />
          <div className="ant-upload-text" id="uppp">
            上传视频
          </div>
        </div>
      );
    }
  }

  private getFileList(): UploadFile<any>[] {
    if (this.props.value) {
      return [
        {
          uid: this.props.value,
          name: this.props.value,
          url: this.props.value,
        },
      ];
    }
    return [];
  }

  async handleRequest(p: any) {
    console.log(p);
    message.config({
      top: 300,
    });
    message.loading('正在上传', 0);
    let formData = new FormData();
    formData.append(p.filename, p.file);
    const request = new Request(p.action, {
      method: 'post',
      body: formData,
    });

    const resp:
      | ResponseDataInterface<string>
      | ResponseErrorInterface = await fetch(request).then((res) => res.json());
    console.log(resp);
    if (resp.err) {
      message.error('上传失败');
    } else {
      if (resp.data) {
        message.destroy();
        message.success('上传成功');
        this.props.onChange && this.props.onChange(resp.data);
      }
    }
  }

  render() {
    return (
      <div>
        <Upload
          action="/api/upload"
          name="imgfile"
          accept=".mp4,.jpg"
          listType="picture-card"
          fileList={this.getFileList()}
          customRequest={this.handleRequest.bind(this)}
          onRemove={() => {
            this.props.onChange && this.props.onChange('');
          }}
          onPreview={() => {
            this.setState({
              showModal: true,
            });
          }}
        >
          {this.getUploadContent()}
        </Upload>
        <Modal
          visible={this.state.showModal}
          footer={null}
          onCancel={() => {
            this.setState({
              showModal: false,
            });
          }}
        >
          <img alt="" style={{ width: '100%' }} src={this.props.value} />
        </Modal>
      </div>
    );
  }
}
