import React, { Component } from 'react';
import { RouteComponentProps } from 'react-router';
import { ThemeInterface, ThemeService } from '../../services/themeService';
import './handleTable.css';

interface IParams {
  id: string;
}

interface EditProps {
  theme: ThemeInterface;
}

export default class EditTheme extends Component<
  RouteComponentProps<IParams> & EditProps
> {
  state = {
    theme: {
      project: '',
      link: '',
    },
  };

  componentDidMount() {
    ThemeService.getThemeById(this.props.match.params.id).then((data) => {
      console.log(data);
      this.setState({
        theme: data.data,
      });
    });
  }
  render() {
    return (
      <div className="editWrapper">
        <div className="title">{this.state.theme.project}</div>
        <div className="vedioArea">
          {this.state.theme.link.endsWith('.jpg') ? (
            <img src={this.state.theme.link} alt="学习主题" className="img" />
          ) : this.state.theme.link.endsWith('.mp4') ? (
            <video
              src={this.state.theme.link}
              controls={true}
              className="img"
            ></video>
          ) : (
            <video
              src="http://152.136.230.239/upload/1590596605276.mp4"
              controls={true}
              className="img"
            ></video>
          )}
        </div>
      </div>
    );
  }
}
