import { LoginConditionInterface } from '../../services/commonTypes';
import { UserInterface } from '../../services/userService';
import {
  loginActionTypes,
  SaveUserType,
  SetLoginContitionType,
  SetLoginUserType,
  changeUserType,
} from '../actions/loginAction';

// 描述学习主题的状态类型
export interface UserState {
  data: UserInterface[]; // 详细数据
  condition: Required<LoginConditionInterface>; // 查询条件
  user: Required<LoginConditionInterface>; // 查询条件
  username: string;
  tags: string[];
}

const defaultstate: UserState = {
  data: [],
  condition: {
    username: '',
    password: 12345678,
  },
  user: {
    username: '',
    password: 0,
  },
  username: '',
  tags: [],
};

function saveUser(state: UserState, action: SaveUserType): UserState {
  return {
    ...state,
    data: action.payload,
  };
}

function setLoginCondition(
  state: UserState,
  action: SetLoginContitionType
): UserState {
  const newState = {
    ...state,
    condition: {
      ...state.condition,
      ...action.payload,
    },
  };
  return newState;
}

function setLoginUser(state: UserState, action: SetLoginUserType): UserState {
  const newState = {
    ...state,
    user: {
      ...state.condition,
      ...action.payload,
    },
  };
  return newState;
}

function changeUser(state: UserState, action: changeUserType): UserState {
  const newState = {
    ...state,
    username: action.payload.username,
    tags: action.payload.tags,
  };
  return newState;
}

export default function userReducer(
  state: UserState = defaultstate,
  action: any
): UserState {
  switch (action.type) {
    case loginActionTypes.saveUser:
      return saveUser(state, action);
    case loginActionTypes.loginCondition:
      return setLoginCondition(state, action);
    case loginActionTypes.loginUser:
      return setLoginUser(state, action);
    case loginActionTypes.changeUser:
      return changeUser(state, action);
    default:
      return state;
  }
}
