import { Dispatch } from 'react';
import { connect } from 'react-redux';
import { RootState } from '../themeList/allThemeList';
import AddTags, {
  AddTagsPropEvents,
} from '../../components/handleThemeTable/addTagsTable';
import { UserState } from '../../redux/reducers/userReducer';
import {
  changeUserAction,
  asyncChangeUserAction,
} from '../../redux/actions/loginAction';

function mapStateToProps(state: RootState): UserState {
  return state.userReducer;
}

function mapDispatchToProps(dispatch: Dispatch<any>): AddTagsPropEvents {
  return {
    onEdit(username, tags) {
      dispatch(changeUserAction(username, tags));
      dispatch(asyncChangeUserAction());
    },
  };
}

export const AddTagsList = connect(
  mapStateToProps,
  mapDispatchToProps
)(AddTags);
