import { User } from './actionTypes';
import { LoginConditionInterface } from '../../services/commonTypes';
// import { UserInterface } from '../../services/userService';

export const loginActionTypes = {
  loginUser: Symbol('loginUser'),
  loginCondition: Symbol('loginCondition'),
  loginData: Symbol('loginData'),
  fetchUser: Symbol('fetchUser'),
  clearUser: Symbol('clearUser'),
  saveUser: Symbol('saveUser'),
  changeUser: Symbol('changeUser'),
  asyncChangeUser: Symbol('asyncChangeUser'),
};

// 触发用户验证的action
export type SetLoginUserType = User<LoginConditionInterface>;

export function setLoginUserAction(
  user: LoginConditionInterface
): SetLoginUserType {
  return {
    type: loginActionTypes.loginUser,
    payload: user,
  };
}

// 触发条件改变的action
export type SetLoginContitionType = User<LoginConditionInterface>;

export function setLoginContitionAction(
  condition: LoginConditionInterface
): SetLoginContitionType {
  return {
    type: loginActionTypes.loginCondition,
    payload: condition,
  };
}

// 保存数据和总数的action
export type SaveUserType = User<any>;

export function saveUsersAction(users: any): SaveUserType {
  return {
    type: loginActionTypes.saveUser,
    payload: users,
  };
}

// 改变用户标签等
export type changeUserType = User<{
  username: string;
  tags: string[];
}>;

export function changeUserAction(
  username: string,
  tags: string[]
): changeUserType {
  return {
    type: loginActionTypes.changeUser,
    payload: {
      username,
      tags,
    },
  };
}

// 触发异步得到数据的action
export function fetchUserAction() {
  return {
    type: loginActionTypes.fetchUser,
  };
}

// 触发异步删除数据的action
export function clearUserAction() {
  return {
    type: loginActionTypes.clearUser,
  };
}

export function asyncChangeUserAction() {
  return {
    type: loginActionTypes.asyncChangeUser,
  };
}
