import { Dispatch } from 'react';
import { connect } from 'react-redux';
import Login, { LoginProps } from '../../components/login/login';
import { UserState } from '../../redux/reducers/userReducer';
import {
  setLoginContitionAction,
  fetchUserAction,
} from '../../redux/actions/loginAction';

export interface RootState {
  userReducer: UserState;
}

function mapStateToProps(state: RootState): UserState {
  return state.userReducer;
}

function mapDispatchToProps(dispatch: Dispatch<any>): LoginProps {
  return {
    onLogin(username, password) {
      console.log(username, password);
      dispatch(setLoginContitionAction({ username, password }));
      dispatch(fetchUserAction());
    },
  };
}
export const LoginList = connect(mapStateToProps, mapDispatchToProps)(Login);
