import { takeEvery, select, call, put } from 'redux-saga/effects';
import { setLoadingAction } from '../actions/ThemeAction';
import { loginActionTypes, saveUsersAction } from '../actions/loginAction';
import { UserService } from '../../services/userService';

function* fetchUsers() {
  yield put(setLoadingAction(true));
  const condition = yield select((state) => state.userReducer.condition);
  console.log(condition);
  const resp = yield call(UserService.getUsers, condition);
  console.log(resp);
  yield put(saveUsersAction(resp.data[0]));
  const state = yield select((state) => state.userReducer);
  console.log(state);
  yield put(setLoadingAction(false));
}

function* clearUsers() {
  yield call(UserService.clearUsers);
}

function* changeUsers() {
  const condition = yield select((state) => state.userReducer);
  yield call(UserService.editUsers, {
    username: condition.username,
    tags: condition.tags,
  });
}

export default function* () {
  yield takeEvery(loginActionTypes.fetchUser, fetchUsers);
  yield takeEvery(loginActionTypes.clearUser, clearUsers);
  yield takeEvery(loginActionTypes.asyncChangeUser, changeUsers);
}
