import React, { Component } from 'react';
import VedioUpLoad from '../../components/vedioUpload/vedioUpLoad';

export default class Vedio extends Component {
  state = {
    img: '',
  };
  render() {
    return (
      <div>
        <VedioUpLoad
          value={this.state.img}
          onChange={(newUrl) => {
            this.setState({
              img: newUrl,
            });
          }}
        />
      </div>
    );
  }
}
