import axios from 'axios';
import {
  ResponsePageDataInterface,
  ResponseDataInterface,
  ResponseErrorInterface,
  EditUserInterface,
  SearchUserConditionInterface,
} from './commonTypes';

export interface UserInterface {
  // 用户姓名
  username: String;
  // 用户密码
  password: Number;
  // 用户标签
  tags: String[];
  isManger: Boolean;
}
export class UserService {
  public static async getUsers(
    condition: SearchUserConditionInterface
  ): Promise<ResponsePageDataInterface<UserInterface>> {
    const resp = await axios.post('/api/login', condition);
    return resp.data;
  }
  public static async getOneUser(
    username: string
  ): Promise<ResponsePageDataInterface<UserInterface>> {
    const resp = await axios.post('/api/login/one', { username });
    return resp.data;
  }

  public static async editUsers(
    condition: EditUserInterface
  ): Promise<ResponseDataInterface<true> | ResponseErrorInterface> {
    const resp = await axios.put('/api/login', condition);
    return resp.data;
  }

  public static async clearUsers(): Promise<void> {
    const resp = await axios.post('/api/login/out');
    console.log(document.cookie);
    return resp.data;
  }
}
