import React, { Component } from 'react';
import { Row, Input, Button, Pagination } from 'antd';
import { ChangeType } from '../../redux/actions/actionTypes';
import './themeTable.css';
import ThemeCard from './themeCard';
import { RootState } from '../../pages/themeList/allThemeList';
import { log } from '../login/login';
import Ad from './ad';

export interface FirstPageEvents {
  onLoad: () => void;
  onSwitchChange: (type: ChangeType, newState: any, id: string) => void;
  onKeyChange: (key: string) => void;
  onSearch: () => void;
  onChange: (newPage: number) => void;
}

export default class RecommendThemes extends Component<
  FirstPageEvents & RootState & log
> {
  state = {
    username: '',
  };

  componentDidMount() {
    if (this.props.onLoad) {
      this.props.onLoad();
    }
  }
  handleChange(page: number) {
    this.props.onChange(page);
  }

  render() {
    let cardList;
    if (this.props.themeReducer.data) {
      cardList = this.props.themeReducer.data.map((item, id) => {
        return (
          <ThemeCard
            key={id}
            id={id}
            data={item}
            onSwitchChange={this.props.onSwitchChange}
          />
        );
      });
    }

    return (
      <div className="firstPageWrapper">
        <Input
          value={this.props.themeReducer.condition.key}
          placeholder="搜索关键字"
          onChange={(e) => this.props.onKeyChange(e.target.value)}
          onPressEnter={this.props.onSearch}
          className="input"
        />
        <Button onClick={this.props.onSearch} className="button">
          搜索
        </Button>
        <Ad />
        <Row gutter={16}>{cardList}</Row>
        <Pagination
          className="page"
          hideOnSinglePage
          current={this.props.themeReducer.condition.page}
          pageSize={this.props.themeReducer.condition.limit}
          total={this.props.themeReducer.total}
          onChange={this.handleChange.bind(this)}
        />
      </div>
    );
  }
}
