import { Dispatch } from 'react';
import { ThemeState } from '../../redux/reducers/themeReducer';
import { addAction, addThemeAction } from '../../redux/actions/ThemeAction';
import { connect } from 'react-redux';
import { RootState } from '../themeList/allThemeList';
import AddTheme, {
  AddPropEvents,
} from '../../components/handleThemeTable/addThemeTable';

function mapStateToProps(state: RootState): ThemeState {
  return state.themeReducer;
}

function mapDispatchToProps(dispatch: Dispatch<any>): AddPropEvents {
  return {
    onAdd(key) {
      key.recommendToTags = key.recommendToTags.split(' ');
      dispatch(addAction(key));
      dispatch(addThemeAction());
    },
  };
}

export const AddThemeList = connect(
  mapStateToProps,
  mapDispatchToProps
)(AddTheme);
