import { Dispatch } from 'react';
import { connect } from 'react-redux';
import { UserState } from '../../redux/reducers/userReducer';
import { clearUserAction } from '../../redux/actions/loginAction';
import Header, { HeaderProp } from '../../components/header/header';

export interface RootState {
  userReducer: UserState;
}

function mapStateToProps(state: RootState): UserState {
  return state.userReducer;
}

function mapDispatchToProps(dispatch: Dispatch<any>): HeaderProp {
  return {
    onClick() {
      dispatch(clearUserAction());
    },
  };
}
export const HeaderList = connect(mapStateToProps, mapDispatchToProps)(Header);
