import React, { Component } from 'react';
import './header.css';
import logo from '../../assets/logo.png';
// import { UserService } from '../../services/userService';
import { Button } from 'antd';
import getCookie from '../../services/cookieServices';
import { UserService } from '../../services/userService';

export interface HeaderProp {
  onClick: () => void;
}

export default class Header extends Component<HeaderProp> {
  state = {
    isManger: false,
  };
  componentDidMount() {
    const username = getCookie('username');
    UserService.getOneUser(username).then((data) => {
      const resp: any = data.data;
      this.setState({
        isManger: resp.isManger,
      });
    });
  }
  render() {
    return (
      <div className="headerStyle">
        <img src={logo} alt="公司logo" />
        <div className="name">范式学习圈内容管理系统</div>
        <ul className="title">
          <li>
            <a href="/">首页</a>
          </li>
          <li>
            <a href="/theme">学习资源</a>
          </li>
          {this.state.isManger === true ? (
            <>
              <li>
                <a href="/theme/add">添加主题</a>
              </li>

              <li>
                <a href="/theme/addtags">添加标签</a>
              </li>
            </>
          ) : null}
        </ul>

        <Button type="primary" href="/login" className="button">
          退出登陆
        </Button>
      </div>
    );
  }
}

