import React from 'react';
import { AllThemeList } from '../../pages/themeList/allThemeList';
import { RecommendThemeList } from '../../pages/themeList/recommendThemeList';
import { AddThemeList } from '../../pages/handleThemeList/addThemeList';
import { HeaderList } from '../../pages/header/headerList';
import { AddTagsList } from '../../pages/handleThemeList/addTagsList';
import PrivateRoute from './protectedRoute';
import VedioList from '../../pages/upLoad/vedioList';
import EditThemeList from '../../pages/handleThemeList/editThemeList';
import './layout.css';

export default function Layout() {
  return (
    <div className="wrapper">
      <div className="header">
        <HeaderList />
      </div>
      <div className="content">
        <PrivateRoute path="/" exact component={RecommendThemeList} />
        <PrivateRoute path="/theme" exact component={AllThemeList} />
        <PrivateRoute path="/theme/add" exact component={AddThemeList} />
        <PrivateRoute path="/theme/edit/:id" exact component={EditThemeList} />
        <PrivateRoute path="/theme/addtags" exact component={AddTagsList} />
        <PrivateRoute path="/theme/vedio" exact component={VedioList} />
      </div>
    </div>
  );
}
