import React, { useState } from 'react';
import { Form, Input, Button, message } from 'antd';
import './handleTable.css';

export interface AddTagsPropEvents {
  onEdit: (username: string, tags: string[]) => void;
}

export default function AddTags(props: AddTagsPropEvents) {
  const [componentSize] = useState('small');
  const [form] = Form.useForm();
  const handleSubmit = () => {
    form
      .validateFields()
      .then((val) => {
        const tags = val.tags.split(' ');
        props.onEdit(val.username, tags);
        message.success('成功添加标签');
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <Form
      className="tagWrapper"
      labelCol={{ span: 6 }}
      wrapperCol={{ span: 14 }}
      layout="horizontal"
      initialValues={{ size: componentSize }}
      onFinish={handleSubmit}
      form={form}
    >
      <Form.Item
        label="用户名"
        name="username"
        rules={[{ required: true, message: '请输入用户名' }]}
        key="用户名"
      >
        <Input />
      </Form.Item>

      <Form.Item
        label="课程标签"
        name="tags"
        rules={[{ required: true, message: '请输入标签' }]}
      >
        <Input />
      </Form.Item>

      <Form.Item>
        <Button type="primary" htmlType="submit" className="button">
          Submit
        </Button>
      </Form.Item>
    </Form>
  );
}
