import React, { useState } from 'react';
import { Form, Input, Button, message } from 'antd';
import VedioUpLoad from '../vedioUpload/vedioUpLoad';
import { withRouter } from 'react-router-dom';
import './handleTable.css';

export interface AddPropEvents {
  onAdd: (par: any) => void;
}

function AddTheme(props: any) {
  const [componentSize] = useState('small');
  const [form] = Form.useForm();
  const handleSubmit = () => {
    form
      .validateFields()
      .then((val) => {
        console.log(val);
        props.onAdd(val);
        message.success('添加成功', 1, () => {
          props.history.push('/theme');
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <Form
      className="addThemeWrapper"
      labelCol={{ span: 6 }}
      wrapperCol={{ span: 14 }}
      layout="horizontal"
      initialValues={{ size: componentSize }}
      onFinish={handleSubmit}
      form={form}
    >
      <Form.Item
        label="学习主题"
        name="project"
        rules={[{ required: true, message: '请输入学习主题' }]}
        key="学习主题"
      >
        <Input key="学习主题" />
      </Form.Item>

      <Form.Item
        label="分享人"
        name="sharePerson"
        rules={[{ required: true, message: '请输入分享人' }]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        label="分享人介绍"
        name="description"
        rules={[{ required: true, message: '请输入分享人介绍' }]}
        key="分享人介绍"
      >
        <Input />
      </Form.Item>
      {/* 
      <Form.Item
        label="范大链接"
        name="link"
        key="范大链接"
        rules={[{ required: true, message: '请输入链接' }]}
      >
        <Input />
      </Form.Item> */}

      <Form.Item
        label="课程内容"
        name="themeLink"
        key="课程内容"
        rules={[{ required: true, message: '请上传视频' }]}
      >
        <VedioUpLoad />
      </Form.Item>

      <Form.Item label="背景图片" name="pictureLink" key="背景图片">
        <VedioUpLoad />
      </Form.Item>

      <Form.Item
        label="课程标签"
        name="recommendToTags"
        key="课程标签"
        rules={[{ required: true, message: '请输入课程标签' }]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        label="班长"
        name="monitor"
        key="班长"
        rules={[{ required: true, message: '请输入班长' }]}
      >
        <Input />
      </Form.Item>

      <Form.Item label="学习委员" name="helper" key="学习委员">
        <Input />
      </Form.Item>

      <Form.Item label="支持BP" name="bp" key="支持BP">
        <Input />
      </Form.Item>

      <Form.Item>
        <Button type="primary" htmlType="submit" className="button">
          Submit
        </Button>
      </Form.Item>
    </Form>
  );
}

export default withRouter(AddTheme);
