import React, { Component } from 'react';
import './themeTable.css';
import { ThemeService } from '../../services/themeService';

export default class Ad extends Component {
  state = {
    img: '',
    themeLink: '',
  };
  async componentDidMount() {
    const condition: any = {
      tags: [true],
    };
    const theme = await ThemeService.getThemes(condition);
    this.setState({
      img: theme.data[0].pictureLink,
      themeLink: theme.data[0].themeLink,
    });
  }
  render() {
    return (
      <div className="adWrapper">
        <a href={this.state.themeLink}>
          <img src={this.state.img} alt="范式讲堂" />
        </a>
      </div>
    );
  }
}
