import React from 'react';
import { Form, Input, Button, Checkbox } from 'antd';
import { UserService } from '../../services/userService';
import './login.css';

export type val = {
  username: string;
  password: number;
  remember?: boolean;
};

export interface log {
  history: {
    push: (a: string) => void;
  };
}

export interface LoginProps {
  onLogin: (username: string, password: number) => void;
}

export default function Login(props: LoginProps & log) {
  const [form] = Form.useForm();
  const handleSubmit = () => {
    form
      .validateFields()
      .then((value) => {
        const { username, password } = value;
        console.log(username, password);
        props.onLogin(username, password);
        const obj: any = { username, password };
        UserService.getUsers(obj).then((data) => {
          if (!data.err) {
            props.history.push('/');
          } else {
            alert('用户名/密码错误');
          }
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const layout = {
    labelCol: { span: 10 },
    wrapperCol: { span: 6 },
  };
  const tailLayout = {
    wrapperCol: { offset: 12, span: 6 },
  };
  return (
    <div className="loginWrapper">
      <h1 className="loginTitle">欢迎来到范式学习圈</h1>
      <Form
        {...layout}
        name="basic"
        form={form}
        initialValues={{ remember: true }}
        onFinish={handleSubmit}
      >
        <Form.Item
          label="用户名"
          name="username"
          rules={[{ required: true, message: '请输入用户名' }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="密码"
          name="password"
          rules={[{ required: true, message: '请输入密码' }]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item {...tailLayout} name="remember" valuePropName="checked">
          <Checkbox>Remember me</Checkbox>
        </Form.Item>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit" className="button">
            登陆
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}
